#cartImg{
    height:4em;
    width:4em;
}

#cart{
    display:flex;
    justify-content: space-evenly;
    align-items: center;
    border:1px solid black;
    flex-direction: row;
    margin:1em;
}

#subCart{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    border-top:1px solid black;
    border-bottom:0.05px solid black;
    min-width:10em;
    width:35em;
    max-width:40em;
    margin:1em;
}



#cartDisplay{
    display:flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

#checkout{
    display: flex;
    flex-direction: row;
    text-align: center;
}

#clear{
    background-image: linear-gradient(rgb(125, 197, 238), rgb(0, 140, 221) 85%, rgb(48, 162, 228));
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serif;
    font-size: 14px;
    position: relative;
    padding: 0px 12px;
    display: block;
    height: 30px;
    line-height: 30px;
    color: rgb(255, 255, 255);
    font-weight: bold;
    border-radius: 4px;
    width:8.6em;
    margin-top:1em;
    margin-left:1em;
}

#test{
    margin-left:1em;
}

@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3) { 
   #subCart{
       width:5em;
       flex-direction: column;
       justify-content: center;
       align-items: center;
       height:17em;
   }
   #cartDisplay{
       margin-left:4em;
   }
   #subCart img,h2,h3{
       margin-bottom:0;
   }
   #clear{
       margin-left:.8em;
   }
   #test{
       margin-left:.8em;
   }

}

@media only screen 
and (min-device-width : 375px) 
and (max-device-width : 667px) {
    #subCart{
        width:5em;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        height:17em;
    }
    #cartDisplay{
        margin-left:4em;
    }
    #subCart img,h2,h3{
        margin-bottom:0;
    }
    #clear{
        margin-left:.8em;
    }
    #test{
        margin-left:.8em;
    }
}
        


    /* iphone 5 */
    @media only screen and (min-device-width: 320px) and (max-device-height: 568px) and (-webkit-device-pixel-ratio: 2) { 
        #subCart{
            width:5em;
            flex-direction: column;
            justify-content: center;
            align-items: center;
            height:17em;
        }
        #cartDisplay{
            margin-left:7em;
        }
        #subCart img,h2,h3{
            margin-bottom:0;
        }
        #clear{
            margin-left:.8em;
        }
        #test{
            margin-left:.8em;
        }
     }