#main{
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
}


#gallery{
    Width:100%;
    display: flex;
    justify-content: space-evenly;
}



.icon{
    height:3em;
    margin-left:1em;
}

#icons{
    display: flex;
    margin-top:2em;
}

#info{
    display:flex;
    margin-bottom:3em;
    justify-content: space-evenly;
    height:30em;
    align-items: center;
    max-width:80em;
    min-width:30em;
    text-align: center;
}

#info img{
    max-width:20em;
    min-width:15em;
    height:20em;
    margin-right:2em;
}

#message{
    font-size:1.5em;
    font-weight: bold;
}

@media only screen 
  and (min-device-width: 375px) 
  and (max-device-width: 812px) 
  and (-webkit-min-device-pixel-ratio: 3) { 
   #gallery{
       width:30em;
   }
   #message{
       font-size:1em;
       font-weight: bold;
   }

}


/* iphone 6, 6s, 7, 8 */
@media only screen and (min-device-width: 375px) and (max-device-height: 667px) and (-webkit-device-pixel-ratio: 2) {
    #message{
        font-size:1em;

    }
    #main h5{
        text-align: center;
        margin-left:3em;
    }
    #icons{
        margin-left:2em;
    }
   }
  
    /* iphone 6+, 6s+, 7+, 8+ */
    @media only screen and (min-device-width: 414px) and (max-device-height: 736px) and (-webkit-device-pixel-ratio: 3) { 
        #message{
            font-size:.9em;
        }
            #gallery{
                width:20em;
            }
        
    }

    
      /* iphone 5 */
      @media only screen and (min-device-width: 320px) and (max-device-height: 568px) and (-webkit-device-pixel-ratio: 2) { 
        #main h5{
            text-align: center;
            margin-left:7em;
        }
        #icons{
            margin-left:5em;
        }
     }




/*Note 9*/
@media
  only screen and (-webkit-min-device-pixel-ratio: 3.5),
  only screen and (   min--moz-device-pixel-ratio: 3.5),
  only screen and (     -o-min-device-pixel-ratio: 3.5/1),
  only screen and (        min-device-pixel-ratio: 3.5),
  only screen and (                min-resolution: 516dpi),
  only screen and (                min-resolution: 3.5dppx) { 

    #info img{
        margin-right:0;
        width:5em;
    }

    #info p{
        width:10em;
        height:10em;
        margin-bottom:22em;
    }
 
}
           


